      width: calc(var(--spacing) * 5);
    }
  }
  .\[\&\:\:-webkit-slider-thumb\]\:cursor-pointer {
    &::-webkit-slider-thumb {
      cursor: pointer;
    }
  }
  .\[\&\:\:-webkit-slider-thumb\]\:appearance-none {
    &::-webkit-slider-thumb {
      appearance: none;
    }
  }
  .\[\&\:\:-webkit-slider-thumb\]\:rounded-full {
    &::-webkit-slider-thumb {
      border-radius: calc(infinity * 1px);
    }
  }
  .\[\&\:\:-webkit-slider-thumb\]\:bg-\[var\(--accent-blue\)\] {
    &::-webkit-slider-thumb {
      background-color: var(--accent-blue);
    }
  }
  .\[\&\:\:-webkit-slider-thumb\]\:shadow-lg {
    &::-webkit-slider-thumb {
      --tw-shadow: 0 10px 15px -3px var(--tw-shadow-color, rgb(0 0 0 / 0.1)), 0 4px 6px -4px var(--tw-shadow-color, rgb(0 0 0 / 0.1));
      box-shadow: var(--tw-inset-shadow), var(--tw-inset-ring-shadow), var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow);
    }
  }
  .\[\&\>option\]\:bg-\[var\(--bg-secondary\)\] {
    &>option {
      background-color: var(--bg-secondary);
    }
  }
  .\[\&\>option\]\:text-\[var\(--text-primary\)\] {
    &>option {
      color: var(--text-primary);
    }
  }
}
:root {
  --bg-primary: #0a0e14;
  --bg-secondary: #111820;
  --bg-tertiary: #1a2332;
  --bg-card: #141c28;
  --border-color: #243044;
  --border-active: #3d5a80;
  --text-primary: #e8edf4;
  --text-secondary: #8899ab;
  --text-muted: #5c6f82;
  --accent-blue: #4a9eff;
  --accent-red: #ff5c5c;
  --accent-orange: #ffaa4a;
  --accent-green: #4aff9f;
  --accent-purple: #a855f7;
  --zone-dorm: #6366f1;
  --zone-common: #22c55e;
  --zone-apparatus: #f59e0b;
  --zone-office: #ec4899;
  --zone-kitchen: #14b8a6;
  --background: var(--bg-primary);
  --foreground: var(--text-primary);
}
body {
  background: var(--bg-primary);
  color: var(--text-primary);
  font-family: var(--font-sans);
  min-height: 100vh;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--bg-secondary);
}
::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--border-active);
}
.gradient-fire {
  background: linear-gradient(135deg, #ff5c5c 0%, #ff8c4a 100%);
}
.gradient-medical {
  background: linear-gradient(135deg, #4a9eff 0%, #4affea 100%);
}
.gradient-all {
  background: linear-gradient(135deg, #a855f7 0%, #ff5caa 100%);
}
@keyframes pulse-alert {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(255, 92, 92, 0.4);
  }
  50% {
    box-shadow: 0 0 20px 4px rgba(255, 92, 92, 0.2);
  }
}
@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
}
.animate-pulse-alert {
  animation: pulse-alert 2s infinite;
}
.animate-blink {
  animation: blink 1s infinite;
}
